.hospital-list-container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.hospital-list-container h2 {
    color: #333;
    text-align: center;
    margin-bottom: 25px;
}

.actions-bar {
    margin-bottom: 20px;
    text-align: right; /* Aligns the button to the right */
}

/* Styles for the actions buttons within the table */
.hospital-actions button {
    margin-left: 8px; /* Space between buttons */
}

.error-message {
    color: #d32f2f;
    text-align: center;
    padding: 20px;
    border: 1px solid #d32f2f;
    background-color: #ffebee;
    border-radius: 4px;
}

